.lp-main-background {
	background-image: linear-gradient(62deg, #8ec5fc 0%, #bea2ff 100%);
	&__up {
		width: 905px;
		height: 728px;
		top: -200px;
		left: -200px;
		border-radius: 50%;
		position: absolute;
		z-index: -1;
		background-image: radial-gradient(rgb(198, 217, 251) 0%, rgba(198, 217, 251, 0) 70%); }
	&__left {
		position: absolute;
		bottom: -120px;
		left: 0;
		@include r(785) {
			bottom: -180px; }
		@include r(377) {
			bottom: -57px;
			left: 374px;
			width: 835px;
			height: 376px; } }
	&__circle-composite {
		left: 50%;
		transform: translateX(-50%);
		top: 220px;
		border-color: rgba(255,255,255,0.2);
		@include r(1041) {
			top: 118px;
			width: 1200px;
			height: 1200px; }
		@include r(377) {
			top: 50px;
			width: 600px;
			height: 600px; }
		&::before {
			left: 53%;
			transform: translateX(-53%);
			top: 148px;
			border-color: rgba(255,255,255,0.2);
			@include r(377) {
				top: 75px;
				width: 450px;
				height: 450px; } }
		&::after {
			left: 50%;
			transform: translateX(-50%);
			top: -155px;
			border-color: rgba(255,255,255,0.2);
			@include r(377) {
				top: -75px;
				width: 750px;
				height: 750px; } } }
	&__circle-xs-purple {
		border-color: #846db1;
		&_first {
			top: 106px;
			right: 572px;
			@include r(1041) {
				top: 95px; }
			@include r(785) {
				right: 618px;
				top: 119px; }
