			top: 352px;
			left: 627px; }
		@include r(377) {
			top: 210px;
			left: 824px;
			width: 12px;
			height: 12px;
			border-width: 2px; } }
	&__circle-l {
		top: 468px;
		left: -55px;
		@include r(785) {
			top: 64px;
			left: 448px; }
		@include r(377) {
			top: 101px;
			left: 733px;
			width: 80px;
			height: 80px;
			border: 2px solid #7e9bfd; } }
	&__circle-xl {
