.forms-table {
	border-radius: 10px;
	box-shadow: 0 2px 9px rgba(33, 33, 38, 0.08);
	background-color: #fff;
	cursor: default;
	&_no-border-rad {
		border-radius: 0 0 5px 0; }
	&_no-shadow {
		box-shadow: none; }
	&__row {
		display: flex;
		align-items: center;
		justify-content: space-between;
		position: relative;
		padding-left: 4%;
		padding-right: 4.3%;
		height: 80px;
		border-left: 4px solid transparent;
		@include r(1480) {
			padding-left: 3%;
			padding-right: 3.5%; }
		@include r(1310) {
			padding-left: 3.5%;
			padding-right: 3.3%; }
		@include r(990) {
			align-items: flex-start;
			height: auto;
			padding: 19px 5.5%; }
		@include r(480) {
			padding-left: 4%;
			padding-right: 5%;
			padding-top: 12px;
			padding-bottom: 18px;
			flex-direction: column; }
		@include r(1) {
			background-color: #fff; }
		&_no-pad {
			padding-left: 0;
			padding-right: 0; }
		&_pop-up {
			@include r(1310) {
				padding-left: 4.5%;
				padding-right: 7%;
				&::after {
					left: 4.5%;
					right: 7%; } }
			@include r(990) {
				padding-left: 6.5%;
				padding-right: 6.5%;
				&::after {
					left: 6.5%;
					right: 6.5%; } }
			@include r(700) {
				padding-top: 12px;
				padding-bottom: 18px; }
			@include r(460) {
				padding-left: 10.5%; } }
		&_users.forms-table__row {
			padding-left: 201px;
			&::before {
				display: none; }
			@include r(1480) {
				padding-left: 174px;
				padding-right: 4.3%; }
			@include r(1310) {
				padding-left: 40px;
				padding-right: 20px; }
			@include r(990) {
				padding-top: 22px;
				padding-bottom: 22px;
				padding-right: 55px; }
			@include r(730) {
				flex-direction: column;
				padding-left: 25px;
				padding-right: 40px; }
			@include r(460) {
				padding-left: 10px;
				padding-top: 10px; }
			&:hover {
				cursor: default;
				background-color: transparent;
				&::after {
					background-color: #e8ebf4;
					left: 201px;
					right: 4.3%;
					@include r(1480) {
						left: 174px;
						right: 4.3%; }
					@include r(1310) {
						left: 40px;
						right: 20px; }
					@include r(990) {
						right: 55px; }
					@include r(730) {
						left: 25px;
						right: 40px; }
					@include r(460) {
						left: 10px;
						right: 40px; } } }
			&::after, &.is-selected::after {
				left: 201px;
				@include r(1480) {
					left: 174px;
					right: 4.3%; }
				@include r(1310) {
					left: 40px;
					right: 20px; }
				@include r(990) {
					right: 55px; }
				@include r(730) {
					left: 25px;
					right: 40px; }
				@include r(460) {
					left: 10px;
					right: 40px; } } }
		&_bg.forms-table__row_heading {
			background-color: #f9f9fb;
			&:hover {
				background-color: #f9f9fb; }
			&::after {
				display: none; } }
		&::after {
			content: '';
			position: absolute;
			bottom: -1px;
			left: 4%;
			right: 4.3%;
			height: 1px;
			background-color: #e8ebf4;
			z-index: 1;
			@include r(1480) {
				left: 3%;
				right: 3.5%; }
			@include r(1310) {
				left: 3.5%;
				right: 3.3%; }
			@include r(990) {
				left: 5.5%;
				right: 5.5%; }
			@include r(480) {
				left: 4%;
				right: 5%; } }
		&:hover, &.is-selected {
			cursor: pointer;
			background-color: #eef7ff;
			.select-row-btn {
				border-color: #699ad2; }
			&::after {
				background-color: #fff;
				right: 0;
				left: -4px; }
			&::before {
				content: '';
				position: absolute;
				top: 0;
				background-color: #fff;
				left: -4px;
				right: 0;
				height: 1px;
				z-index: 1; } }
		&:last-child {
			&:hover {
				border-radius: 0 0 10px 10px;
				&::after {
					display: none; } }
			&.is-selected {
				border-radius: 0;
				&::after {
					display: none; } } }
		&_heading {
			&::before {
				display: none; }
			@include r(990) {
				height: 80px;
				align-items: center; }
			@include r(480) {
				height: 60px; }
			&:hover, &.is-selected {
				cursor: default;
				background-color: transparent;
				.select-row-btn {
					border-color: #e8ebf4;
					&:hover {
						border-color: #699ad2; } }
				&::after {
					background-color: #e8ebf4;
					left: 4%;
					right: 4.3%; } } }
		&.is-selected {
			border-left: 4px solid #699ad2;
			background-color: #eef7ff; }
		&_pop-up {
			padding-left: 4.5%;
			padding-right: 2.6%;
			&::after, &.is-selected::after {
				left: 4.5%;
				right: 2.6%; } } }
	&__right {
		display: flex;
		align-items: center;
		justify-content: space-between;
		max-width: 900px;
		width: 100%;
		@include r(1480) {
