.fee {
	color: #3f92cc;
	position: relative;
	left: 10px;
	@include r(480) {
		font-size: 12px; }
	&::before {
		content: '';
		position: absolute;
		width: 6px;
		height: 6px;
		border-radius: 50%;
		background-color: #699ad2;
		left: -20px;
		top: 9px;
		@include r(480) {
			top: 6px;
			left: -15px; } }
	&::after {
		content: '$';
		position: absolute;
		font-size: 16px;
		color: #3f92cc;
		left: -8px;
		top: 1px;
		@include r(480) {
			font-size: 13px;
			top: 0; } }
	&_na {
		color: #52545a;
		left: 1px;
		&::before, &::after {
			display: none; } }
	&_pop-up {
		left: 0;
		&:not(.fee_na) {
			left: 10px; }
		@include r(480) {
			&:not(.fee_na) {
				left: 7px; } } } }

.table-date-title {
	display: block;
	color: #858891;
	font-size: 12px;
	font-weight: 400;
	position: relative;
	top: -5px; }
.table-date {
	display: block;
	color: #52545a;
	font-size: 14px;
	font-weight: 400;
	position: relative;
	top: -2px; }
